@import "../../assets/variables.scss";

.sidebar-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 300px;
  height: 100vh;
  min-height: 100vh;
  min-width: 220px;
  z-index: 5;

  @media screen and (max-width: 900px) {
    width: 70%;
  }

  &__header {
    background-color: var(--color-black);
    display: flex;
    align-items: center;
    padding-left: 20px;

    @media screen and (max-width: 900px) {
      visibility: hidden;
    }

    &__close-btn {
      color: var(--color-link);
      cursor: pointer;
      font-size: 15px;
    }
  }

  &__content {
    padding: 16px 7px 16px 20px;
    background-color: var(--color-black);
    margin-top: 2px;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;

    @media screen and (min-width: 900px) {
      margin-top: 0;
    }

    &__main-btn {
      @include sidebarBtn;
    }
  }
}
