@mixin sidebarBtn {
  display: flex;
  justify-content: space-between;
  padding: 8px 14px 10px 8px;
  color: var(--color-link);
  font-weight: var(--font-weight-bold);
  font-size: var(--font-size-menu-text);
  width: 100%;
  align-items: center;
  border-radius: 4px;
  cursor: pointer;

  &__arrow {
    animation: all 0.2s linear;
    height: 10px;
    width: 14px;
  }

  &__left {
    display: flex;
    align-items: center;
    > img {
      margin-right: 10px;
    }

    > p {
      color: var(--color-link);
      font-weight: var(--font-weight-normal);
      font-size: var(--font-size-menu-text);
      white-space: nowrap;
      text-align: start;

      @media screen and (max-width: 450px) {
        white-space: normal;
      }
    }
  }
}

.page-container {
  display: flex;
  flex-direction: column;
  width: -webkit-fill-available;
  position: absolute;
  z-index: 0;

  @media screen and (max-width: 900px) {
    position: relative;
    left: 0 !important;
  }

  &__image-bottom {
    max-width: 100%;
  }

  &__content {
    padding: 30px 40px;
  }

  &__title {
    font-size: var(--font-size-title);
    font-weight: var(--font-weight-bold);
    margin-bottom: 10px;
    background: linear-gradient(
      147.89deg,
      #ffd86e 15.47%,
      #fdd56c 15.64%,
      #dfb04c 18.09%,
      #c89637 20.65%,
      #ba842a 23.34%,
      #b17a24 26.25%,
      #af7722 29.75%,
      #c9973c 38.21%,
      #e6bc5f 48.23%,
      #f2cb6d 53.76%,
      #ebc365 58.21%,
      #d9ac4f 64.66%,
      #be8a31 72.27%,
      #af7722 76.24%,
      #b37c25 79.51%,
      #ffd86e 79.59%,
      #bf8a2f 83.5%,
      #d5a443 87.84%,
      #f6cc64 92.42%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
  }

  &__subtitle {
    font-size: var(--font-size-subtitle);
    font-weight: var(--font-weight-normal);
    color: var(--text-color);
    margin-bottom: 20px;
  }

  &__text {
    font-size: var(--font-size-text);
    font-weight: var(--font-weight-normal);
    color: var(--text-color);
    margin-bottom: 30px;
  }

  &__top-pic {
    height: 234px;
    background-image: url("./images/pages/beepro-top.png");
    background-size: cover;
    display: flex;
    align-items: center;
    padding-left: 50px;

    &__logo {
      display: flex;
      align-items: center;
      gap: 17px;

      > img {
        height: 100px;
        width: 100px;
      }
      > p {
        font-size: 50px;
        color: #fff;
        font-weight: var(--font-weight-bold);
      }
      @media screen and (max-width: 900px) {
        display: none;
      }
    }
  }
}
